<template>
  <div class="good">
    <div class="left" v-if="hasCheckBox" @click="toggleItem">
      <i ref="checkbox" :class="checked === true ? 'active' : ''"></i>
    </div>
    <div class="middle">
      <img class="img" :src="item.imageUrl" />
    </div>
    <div class="right">
      <p class="name">{{item.title}}</p>
      <div class="main">
        <div class="desc">
          <p class="size">
            <label>规格：</label>
            <span>{{item.desc}}</span>
          </p>
          <p class="price">
            <label>￥</label>
            <span>{{item.price|formatMoney}}</span>
            <label>/{{item.unit}}</label>
          </p>
        </div>
        <div class="operate">
          <i class="minus" @click="minus" v-show="showIcon"></i>
          <div class="input-wrapper" v-show="showIcon">
            <input type="text" v-model="count" />
          </div>
          <i class="plus" @click="plus"></i>
        </div>
        </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {hasClass,addClass,removeClass} from 'common/js/dom'

  export default {
    data () {
      return {
        count: 0,
        checked: false,
        showIcon: false,
        isManualCount: false
      }
    },
    props: {
      hasCheckBox: {
        type: Boolean,
        default: false
      },
      item: {
        type: Object,
        default: () => {
          return {}
        }
      }
    },
    methods: {
      minus() {
        this.isManualCount = false
        this.count = Math.max(0, parseInt(this.count - 1) || 0)
      },
      plus(){
        this.isManualCount = false
        this.count = (parseInt(this.count) || 0) + 1
      },
      // 该方法仅仅用来初始化数据
      setCount(count) {
        this.isManualCount = true
        this.count = count
      },
      setChecked(value) {
        this.checked = value
      },
      toggleItem(){
        let checkbox = this.$refs.checkbox
        if(hasClass(checkbox, 'active')){
          this.checked = false
        } else　{
          this.checked = true
        }
      }
    },
    watch: {
      // 如果监听对象，需要设置deep:true，并且只能监听初始化时候的属性
      // 官方文档讲的很清楚，监听是注册的时候对属性批量使用Object.defineProperty方法实现的，所以后添加的属性不会监听
      count(newValue) {
        if(newValue === 0){
          this.showIcon = false
        } else {
          this.showIcon = true
        }
        if(!this.isManualCount){
          this.$emit('count', {
              ...this.item,
              count:newValue === '' ? 0 : (isFinite(newValue) ? parseInt(newValue) : 0),
              //_trueValue_表示输入框的真实值，用来区分数字0和空字符串''
              // 以便在购物车中的输入操作
              _trueValue_:newValue
          })
        }
      },
      checked(newValue) {
        this.$emit('checked', newValue, this.item)
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .good
      width 100%
      height 100%
      display flex
      background #fff
      .left
        display flex
        justify-content flex-end
        align-items center
        width 24px
        i
          display flex
          background url("./unchecked.png") no-repeat
          background-size contain
          width 13px
          height 13px
          position relative
          &.active
            background url("./checked.png") no-repeat
            background-size contain
          &:before
            position absolute
            left -10px
            top -10px
            right -10px
            bottom -10px
            content ''
      .middle
        width 80px
        height 100%
        display flex
        align-items center
        justify-content center
        img
          display block
          height 70px
          width 70px
      .right
        display flex
        flex 1
        flex-direction column
        margin-right 15px
        border-bottom 1px solid #f3f3f3
        .name
          font-size $font-size-medium-x
          color #4c4c4c
          margin-top 15px
          height 32px
          font-weight bold
        .main
          display flex
          justify-content space-between
          .desc
            display flex
            flex-direction column
            color #4c4c4c
            font-size $font-size-small-x
            .size
              margin-top 12px
              margin-bottom 12px
            .price
              color #eb4747
              label
                font-size $font-size-small-x
              span
                font-weight bold
                font-size $font-size-medium-x
          .operate
            display flex
            justify-content flex-end
            align-items flex-end
            flex-basis 115px
            margin-right 15px
            .minus
              display flex
              background url("./minus.png") no-repeat
              background-size contain
              width 25px
              height 25px
            .input-wrapper
              display flex
              height 23px
              flex-basis 35px
              margin-left 10px
              margin-right 10px
              input
                align-items center
                width 100%
                height 100%
                box-sizing border-box
                border 1px solid #FFC848
                border-radius 6px
                text-align center
            .plus
              display flex
              background url("./plus.png") no-repeat
              background-size contain
              width 25px
              height 25px

</style>



